/*===================
Button Area 
====================*/
a {
  &.btn-transparent {
    // color: var(--color-primary);
    @extend %transition;
    font-size: 14px;
    font-weight: 600;
    position: relative;
    display: inline-block;
    padding-bottom: 3px;
    @extend .theme-gradient;

    &:hover {
      color: var(--color-primary);
    }
    &::after {
      position: absolute;
      content: "";
      left: 0;
      bottom: 0;
      width: 40px;
      height: 2px;
      @extend .bg-theme-gradient;
      opacity: 0.75;
      @extend %transition;
    }
    &:hover {
      &::after {
        width: 100%;
      }
    }
  }
}

a,
button {
  &.rn-btn {
    padding: 0 23px;
    height: 40px;
    display: inline-block;
    line-height: 34px;
    border: 2px solid var(--color-primary);
    border-radius: 4px;
    font-size: 14px;
    position: relative;
    z-index: 2;
    // font-family: Montserrat;
    color: var(--color-primary);
    letter-spacing: 0.2px;
    @extend %transition;
    text-transform: uppercase;

    &.btn-opacity {
      border: 2px solid var(--color-opacity-1);
      color: var(--color-white);
    }

    &:hover {
      border: 2px solid var(--color-primary);
      transform: translateY(-3px);
      color: #ffffff;
      background: var(--color-primary);
    }
    &.btn-white {
      border: 2px solid #ffffff;
      color: #ffffff;
      &:hover {
        border-color: var(--color-primary);
      }
    }
  }
}

button,
a {
  &.rn-button-style--2 {
    color: #c6c9d8;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 2px;
    border: 2px solid #b1b4c1;
    padding: 15px 40px;
    border-radius: 6px;
    display: inline-block;
    font-weight: 500;
    transition: 0.3s;
    @media #{$sm-layout} {
      padding: 9px 25px;
    }
    &.rn-btn-dark {
      border: 2px solid $heading-color;
      color: $heading-color;
      &:hover {
        background: $heading-color;
        color: #fff;
      }
    }
    &.border-opacity-half {
      border-color: rgba(31, 31, 37, 0.1);
    }
    &.rn-btn-small {
      padding: 10px 20px;
    }
    &.btn-primary-color {
      &:hover {
        border: 2px solid var(--color-primary);
        background: var(--color-primary);
        color: #ffffff;
      }
    }
    &.btn-solid {
      border: 2px solid var(--color-primary);
      background: var(--color-primary);
      color: #ffffff;
    }
    &.btn-solid {
      background: var(--color-primary);
      color: #ffffff;
    }
    &:hover {
      background: transparent;
      color: var(--color-primary);
      box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.1);
      transform: translateY(-5px);
    }
  }
}

/*--------------------------
    Button Style Two  
---------------------------*/

.button-group {
