.commodity-price-list {
  display: flex;
  overflow-x: auto;
  margin-top: 40px;
  padding: 10px;
  grid-gap: 15px;
  gap: 15px; /* Space between commodity boxes */
}

.commodity-box {
  width: 400px; /* Adjusted width */
  border: 2px solid #67a961;
  background-color: #f8fff8;
  padding: 20px;
  border-radius: 5px;
  flex-shrink: 0; /* Prevent shrinking in flex container */
  display: flex;
  flex-direction: column;
}

.top-section {
  display: flex;
  flex-direction: column;
  align-items: left;
  margin-bottom: 2px;
}

.price {
  color: #67a961;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 3px;
}

.description {
  font-size: 14px;
  color: #8f9c8f;
  text-align: left;
}

.middle-section hr,
.bottom-section hr {
  border: none;
  border-top: 2px solid rgba(200, 200, 200, 0.7); /* Lighter and more transparent grey */
  margin: 5px 0; /* Adjusted margin for spacing */
}

.middle-section {
  border-top: 1px solid #ddd;
  padding-top: 5px;
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.current-price {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}

.name {
  font-size: 24px; /* Larger font size for the price */
  color: #67a961;
  margin-right: 5px;
  font-weight: bold;
}

.price-container {
  display: flex;
  align-items: center;
  font-size: 20px; /* Adjust for desired size */
  font-weight: bold;
  color: black;
  text-align: left;
}

.price {
  font-size: 40px; /* Larger font size for the price */
  color: black;
  text-align: left;
  font-weight: 600;
}

.arrow {
  font-size: 18px;
  margin-left: 5px;
}

.line-plot {
  width: 40px; /* Width of the dummy line plot */
  height: 10px;
  margin-left: 40px;
  border-radius: 3px;
}

.up {
  color: green;
}

.down {
  color: red;
}

.percentage-change {
  font-size: 14px; /* Adjust for desired size */
  align-self: flex-start;
}

.line-plot {
  font-size: 12px;
  color: gray;
  margin-top: 5px;
}

.percentage-change {
  font-size: 12px;
  color: grey;
  text-align: right;
  margin-bottom: 5px;
}

.bottom-section {
  margin-top: 15px;
}

.local-gov-prices {
  display: flex; /* Set to flex for row alignment */
  justify-content: space-between;
  grid-gap: 5px;
  gap: 5px; /* Space between LGA boxes */
  margin: 20px 0;
}

.lga-box {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  border: 2px solid #8fbc8f; /* Light green border */
  padding: 5px;
  border-radius: 3px;
  width: 100px; /* Set width to fit three boxes in a row */
  height: 80px;
  justify-content: center;
  background: #e5fbe5;
}

.lga-name {
  font-weight: bold;
  font-size: 11px;
  margin-bottom: 4px;
  color: #67a961;
  /* align-self: center; */
}

.lga-price {
  font-size: 11px;
  font-weight: 600;
  color: #333;
  margin-left: 1px;
}

.last-updated {
  font-size: 12px;
  font-weight: 400;
  color: #888;
  text-align: left;
  margin-top: 20px;
}

.commodity-detail-page {
  padding: 20px;
  background-color: #f0f5f0; /* Light green background */
}

.totalContainer {
  border: 2px solid #67a961; /* 2px solid green border */
  border-radius: 5px; /* Optional: add rounded corners if needed */
}

.plot-section {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 40px;
  flex-wrap: wrap; /* Allow wrapping on smaller screens */
}

.plot-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap; /* Wrap elements on smaller screens */
}

.info-box {
  background-color: #67a961; /* Green background */
  color: white;
  padding: 10px;
  border-radius: 5px;
  text-align: center;
  align-content: center;
  margin: 20px;
  min-width: 130px;
  height: 82px;
}

.price-box {
  background-color: #f0f5f0; /* Green background */
  border: 1px solid #67a961;
  color: #82b87f;
  padding: 10px;
  border-radius: 5px;
  text-align: center;
  width: 120px;
  margin: 20px;
  height: 82px;
}

.price-box strong {
  color: #67a961;
  font-size: 24px;
  display: block;
  margin-bottom: 5px;
}

.price-box span {
  font-size: 14px;
}

.plot-controls {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px; /* Add some spacing for mobile view */
}

.control-dropdown {
  margin-left: 10px;
  margin-right: 30px;
}

.plot-container {
  background-color: #e5fbe5; /* Faint green background for the plot */
  padding: 20px 10px 20px 5px; /* Reduce left padding to move plot leftward */
  border-radius: 5px;
}

.custom-chart {
  height: 500px; /* Increased height for the plot */
}

/* Ensure all plot lines are green */
.line-plot,
.bar-plot,
.histogram-plot {
  stroke: #67a961; /* Green line color */
  fill: #67a961; /* Fill color for bars and histograms */
}

.table-container {
  margin-top: 40px;
}

.table th,
.table td {
  vertical-align: middle;
}

.table th {
  width: 40%; /* Give more space to LGA name */
}

.table td:last-child {
  text-align: right;
}

.trend-icon {
  font-weight: bold;
}

.trend-icon.up {
  color: green;
}

.trend-icon.down {
  color: red;
}

/* Responsive Design for Mobile */
@media (max-width: 768px) {
  .commodity-detail-page {
    padding: 10px; /* Reduce padding on mobile */
  }

  .plot-section {
    flex-direction: column; /* Stack items vertically */
    align-items: stretch; /* Align items to fill the container */
  }

  .plot-info {
    justify-content: center; /* Center items on mobile */
    margin-bottom: 10px; /* Add space between elements */
  }

  .info-box,
  .price-box {
    width: 100px; /* Reduce width */
    height: 75px; /* Reduce height */
    margin: 10px 5px; /* Reduce margin */
  }

  .plot-container {
    padding: 10px 5px 10px 0; /* Further reduce left padding on mobile */
  }

  .custom-chart {
    height: 300px; /* Reduce height of the plot on mobile */
  }

  .plot-controls {
    justify-content: center; /* Center plot controls */
  }
}

.commodityWrapper {
  margin: 12rem;
}

@media (max-width: 768px) {
  /* This targets tablets and smaller devices */
  .commodityWrapper {
    margin: 14rem 1rem 3rem;
  }
}

